.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  display: flex;
}

.header__nav {
  justify-content: center;
  align-items: center;
  height: 60px;
  display: flex;
}

.header__nav-list {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin: 0 10px;
  font-size: 12px;
  font-weight: 500;
}

.header__nav-item:first-child {
  margin-left: 0;
