  line-height: 15px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  grid-gap: 46px 48px;
  grid-template-columns: 200px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (width >= 1200px) {
  .main {
    padding: 50px 128px;
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 3px 40px;
  display: block;
}

.card__title {
